.home-page {
  article {
    padding-top: 0.2rem;
    padding-bottom: 0rem;
  }
  .circle-image {
    border-radius: 50%;
  }
  .dg-section {
    box-shadow: 5px 7px 9px -4px rgb(158, 158, 158);
    transition: all 0.75s ease;

    .dg-img {
      height: 210px;
    }

    &:hover {
      .dg-img {
        transition: all 0.75s ease;
        background: #ddd;
        &:hover {
          background: $primary-color;
        }
      }
    }
  }

  .welcome-title {
    h3 {
      border-bottom: 0.9px solid #dedede;
      padding-bottom: 10px;
      font-size: 24px;
      color: $secondary-color;
      font-weight: 800;
      font-family: "Nunito Sans,sans-serif";
    }
    p {
      line-height: 1.5;
      color: gray;
    }
  }

  iframe {
    width: 100%;
    height: 492px;
    border: none;
  }

  .carousel,
  #carousel {
    padding-left: 0px !important;

    .carousel-item {
      height: 450px;
      transition: 15s ease-in-out left;
      // background-color: #fff;
      @include border-radius($card-inner-border-radius);
      // box-shadow: $box-shadow-hover;

      img {
        width: 100%;
        // height: 100%;
      }

      a {
        background: $primary-color;
        color: #000;
        padding: 0.5em;
        border-radius: 5px;
        &:hover {
          background: $primary-color !important;
          color: white;
          transition: all 0.5s ease;
        }
      }
    }

    .carousel-control-prev,
    .carousel-control-next {
      // background-color: rgba(0,0,0,.5) !important;
    }

    .carousel-control-prev {
      margin-left: 20px !important;
    }

    .carousel-control-next {
      margin-right: 20px !important;
    }

    .carousel-control-prev,
    .carousel-control-next {
      position: absolute;
      top: 45%;
      bottom: 45%;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 3.2rem;
      color: #fff;
      text-align: center;
      opacity: 1;
      border-radius: 50%;
    }

    .carousel-caption,
    .mask {
      width: 100%;
      left: 0;
      bottom: 0;
      color: $primary-color !important;
      position: absolute;
      // padding: 0 20px;
      // background: rgba($primary-color,0.7);
      //background-image: linear-gradient(to bottom,rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2), #212121 100%);
      padding: 1.5rem 1rem;
      // transition:

      h1 {
        font-size: 48px;
        color: $primary-color;
        font-weight: 800;
        font-family: "Nunito Sans,sans-serif";
        text-shadow: $primary-color;
        -webkit-animation-name: fadeInLeft;
        animation-name: fadeInLeft;
      }

      a {
        -webkit-animation-name: fadeInUp;
        animation-name: fadeInUp;
      }

      img {
        -webkit-animation-name: slideInRight;
        animation-name: slideInRight;
        display: block;
        height: 200px;
        max-width: 100%;
      }

      p {
        -webkit-animation-name: slideInRight;
        animation-name: slideInRight;
        font-size: 1.6em;
        z-index: 22222;
        position: relative;
        font-weight: 900;
        //text-shadow: 1px 2px #333333;
      }
      .wave-svg {
        position: absolute;
        top: auto;
        bottom: 0;
        z-index: 10;
        pointer-events: none;
        width: 2000px;
        left: 50%;
        margin: 0 0 0 -1000px;
      }
    }

    h1,
    p,
    a,
    .carousel-item .mask img {
      -webkit-animation-duration: 1s;
      animation-duration: 1.2s;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
    }
  }

  .meeting-nav {
    position: relative;
    bottom: 0;
    z-index: 999;
    text-align: center;
    padding: 0;
    margin: 0 auto;
    top: -47px;

    .meeting-dots {
      padding: 11px 90px;
      margin: 0 auto;
      text-align: center;
      float: none;
      width: auto;
      display: inline-block;
      list-style: none;
      position: relative;
      background-color: #edeef0;
      top: -14px;
      border-top-left-radius: 51px;
      border-top-right-radius: 51px;

      &:before {
        content: " ";
        display: block;
        z-index: 99999;
        position: absolute;
        left: -30px;
        bottom: 0;
        top: 21px;
        border-top-left-radius: 0;
        width: 0;
        height: 0;
        border-bottom: 40px solid #edeef0;
        border-left: 40px solid transparent;
      }

      &:after {
        content: " ";
        display: block;
        z-index: 99999;
        position: absolute;
        right: -30px;
        bottom: 0;
        top: 21px;
        border-top-left-radius: 0;
        width: 0;
        height: 0;
        border-bottom: 40px solid #edeef0;
        border-right: 40px solid transparent;
      }

      li {
        display: inline-block;
        border: 2px solid transparent;
        border-radius: 14px;
        margin: 0;
        width: 30px;
        height: 40px;
        text-align: center;
        font-size: 16px;
        padding: 5px 0;
        font-weight: 500;
        color: #000;

        &.active {
          border: 2px solid #0e789d;
        }
      }
    }
  }

  .bg-colors {
    div {
      height: 8px;
    }
    .color-1 {
      background: $secondary-color;
    }
    .color-2 {
      background: $primary-color;
    }
    .color-3 {
      background: white;
    }
    .color-4 {
      background: $secondary-color;
    }
  }

  .highlights {
    margin-bottom: 1rem;
    border-bottom: 1px solid $secondary-color;
    padding-right: 3px;

    .skew {
      flex-shrink: 0 !important;
      position: relative;
      color: rgb(255, 255, 255);
      background: $primary-color;
      display: block;
      padding: 1.4rem 0.25rem 0 0.8rem;
      z-index: 2;
      font-size: 1.4em;

      &:after {
        content: " ";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        background: $primary-color;
        transform-origin: top right;
        transform: skew(40deg, 0deg);
      }
    }

    a {
      span.text-black {
        color: black !important;
      }
    }
  }

  .nav-item {
    margin-top: -0.3rem;

    .nav-link {
      border-radius: 0;
      border: none;
      padding-left: 1rem;
      padding-right: 1rem;
      font-size: 2rem;
      color: #495057;
      border-bottom: 0.3rem solid transparent;
      transition: all 0.3s ease;

      &.active {
        color: $primary-color;
        background-color: transparent;
        border-bottom: 0.3rem solid $primary-color;
      }
    }
  }

  .media {
    .media-body {
      .news-date {
        margin-right: 10px;
        color: #686868;
        font-weight: bold;

        .date-dec {
          font-size: 25px;
          line-height: 25px;
          padding-right: 3px;
          border-right: 2px solid #2d4a84;
        }

        .month-dec {
          font-size: 12;
          padding-right: 3px;
          border-right: 2px solid #2d4a84;
        }
      }
    }
  }

  .news img {
    max-width: 150px;
  }
}

.overlay-container {
  .overlay-inside {
    background: rgba($primary-color, 0.8);
  }

  .title-overlay {
    color: rgba(255, 255, 255, 0.9);
    font: 400 25px / 25px "Roboto-Bold";
