  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    // background: url(../images/shadow_bg.png);
    z-index: -1;
    left: -40px;
    width: 77px;
    height: 332px;
    top: 0;
  }

  &::after {
    content: "";
    position: absolute;
    // background: url(../images/shadow_bg.png);
    z-index: -1;
    right: -25px;
    width: 77px;
    height: 332px;
    top: 0;
  }
  .main-content-inside {
    min-height: 350px;
    // background: white;
  }
