.post-module {
  position: relative;
  z-index: 1;
  display: block;
  background: #ffffff;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.15);
  transition: all 0.3s linear 0s;

  .thumbnail {
    background: #000000;
    //height: 200px;
    overflow: hidden;

    img {
      display: block;
      width: 120%;
      transition: all 0.3s linear 0s;
    }
  }

  .post-content {
    position: absolute;
    bottom: 0;
    background: #ffffff;
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    transition: all 0.3s cubic-bezier(0.37, 0.75, 0.61, 1.05) 0s;

    .description {
      display: block !important;
      height: auto !important;
      opacity: 1 !important;
    }

    .category {
      position: absolute;
      top: -34px;
      left: 0;
      background: $secondary-color;
      padding: 10px 15px;
      color: #ffffff;
      font-size: 14px;
      font-weight: 600;
      text-transform: uppercase;
    }

    .title {
      margin: 0;
      padding: 0 0 10px;
      color: #333333;
      font-size: 26px;
      font-weight: 700;
    }

    .sub_title {
      margin: 0;
    }

    .description {
      display: none;
      color: #666666;
      font-size: 14px;
      line-height: 1.8em;
    }

    .post-meta {
      margin: 30px 0 0;
      color: #999999;

      .timestamp {
        margin: 0 16px 0 0;
      }

      a {
        color: #999999;
        text-decoration: none;
      }
    }
  }

  &:hover,
  .hover {
    box-shadow: 0px 1px 35px 0px rgba(0, 0, 0, 0.3);

    .thumbnail img {
      transform: scale(1.1);
      opacity: 0.6;
    }
  }
}

.online-services {
  .service-item {
    -webkit-transform-origin: center;
    -ms-transform-origin: center;
    transform-origin: center;
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.15);
    //width: 220px;
    height: 216px;
    overflow: hidden;
    text-align: center;
    padding-top: 40px;
    margin-right: 0;
    position: relative;
    transition: all 0.5s ease;

    .service-btn-wrapper {
      position: absolute;
      width: 100%;
      height: 40px;
      left: 0;
      bottom: -20%;
      opacity: 0;
      transition: all 0.5s ease;
      transition-delay: 0.15s;

      .serv-btn {
        text-align: center;
        color: #fff;
        font: normal 12px/35px Lato-Regular, DroidArabicKufi, sans-serif;
        border-radius: 35px;
        margin: 0 0.5%;
        height: 35px;
        background: $secondary-color;
        position: relative;
        display: inline-block;
        transition: all 0.3s ease;
        padding: 0 20px;
      }
      a:hover,
      a:focus {
        text-decoration: none;
      }
    }

    &:hover {
      background-color: #ffffff;
      transform: scale(1.1);
      box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.2);
      z-index: 1;

      .service-btn-wrapper {
        bottom: 15px;
        opacity: 1;
      }
    }
