  @keyframes move-up {
    0% {
      transform: translateY(50px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  
  @keyframes move-down {
    0% {
      transform: translateY(-50px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  
  @keyframes move-left {
    0% {
