  .progress-body {
    position: relative;
    width: 90%;
    position: relative;
    margin: auto;
    cursor: pointer;
  
    .media {
      justify-content: center;
    }
  
    &::before {
      content: "";
      display: block;
      height: 4px;
      width: 90%;
      margin: auto;
      z-index: 1;
      border-radius: 2px;
      background-color: #eeeeee;
      position: absolute;
      bottom: 5px;
    }
  
    .progress {
      display: block;
      height: 4px;
      border-radius: 2px;
      position: absolute;
      bottom: 5px;
      z-index: 2;
      left: 0;
      overflow: hidden;
  
      div {
        height: 100%;
        float: left;
        position: relative;
        z-index: 3;
        width:100%;
        @media screen {
          width: 0%;
          animation: progressAnimation 4s;
          animation-delay: 0.3s;
          animation-direction: alternate;
          animation-fill-mode: forwards;
        }
       
      }
    }
  }
  
