//@ for creating a blink item
.new-star{
    color: red;
    font-size: 2em;
    animation: CSSblink linear 1s infinite;
}

.rightCSS a:nth-child(even) .new-star{
    animation: CSSblink2 linear 1s infinite;
}

@keyframes CSSblink {
    0% { opacity: 0 }
    50% { opacity: 1 }
    100% { opacity: 0 }
}
@keyframes CSSblink2 {
    0% { opacity: 1 }
    50% { opacity: 0 }
    100% { opacity: 1 }
}

//@ end of creating blink item...


