.hover-shadow{
    transition:box-shadow 0.4s, top 0.3s;
    box-shadow: 0 0rem 0rem rgba(0, 0, 0, 0);
    top:0;
    position:relative;

    &:hover{
        box-shadow: 0 0.5rem 0.2rem rgba(0, 0, 0, 0.15);
        top:-0.5rem;
    }
}

.hover-border{
    transition:border 0.4s;
    border:1px solid rgba(0, 0, 0, 0);


    &:hover{
        border:1px solid rgba(0, 0, 0, 0.15);
    }
}

.hover-slide{
    position: relative;
    transition:left 0.3s;
    left:0px;

    &:hover{
        left:5px;
    }
}

.pointer-hover{
    overflow: hidden;

    .fa{
        transition:left 0.3s,transform 0.3s,opacity 0.3s;
        position: relative;
        
    }

  

    .fa:nth-child(1){
        left:-50px;
    }

    .fa:nth-child(2){
        transform:scale(1,1);
        opacity: 1;
        position:absolute;
    }

    // .fa-play{
    //     left:0px !important;
    //     position: relative !important;
    // }

    .container-overlay{
        opacity: 0.5;
    }

    &:hover{
        .fa:nth-child(1){
            left:0;
        }

        .fa:nth-child(2){
            transform:scale(0.5,0.5);
            opacity: 0;
        }

        // .fa-play{
        //     left:0px !important;
        //     position: relative !important;
        // }

        .play-bg{
            border-radius: 10%;
            width:80px;
        }
        
        .container-overlay{
            opacity: 1;
        }
    }
}

.container-overlay{
    background: linear-gradient(to bottom, rgba($primary-color-dark, 0) 0%, rgba($primary-color,0.8) 100%);
    transition: opacity 0.4s, background 0.4s;
    opacity: 0.2;
    width: 100%;
    height: 100%;
    position:absolute;
    left:0;top:0;
}

.has-show-on-hover{
    .show-on-hover{transition: all 0.3s;opacity:0;}
    &:hover .show-on-hover {opacity:1}
}

.text-accent{
    color:$accent-color !important;
}

.show-more-content{
    position: relative; 
    overflow: hidden;
    transition:color 0.3s;
    color:white;
   // border:1px solid black;

    .container-overlay{
        opacity: 0;
        transition: opacity 0.4s, background 0.4s;
        transition-delay: 0.1s;
        background: linear-gradient(to bottom, rgba($secondary-color, 0) 0%, rgba($secondary-color,0.6) 100%);
    }

    .dark-overlay{
        transition:background 0.4s,opacity 0.4s;
        background-color:rgba(#333,0.4);
    }

    .blend-overlay{
        transition:background 0.4s,opacity 0.4s;
        background-color:rgba(#333,0.4);
        mix-blend-mode: color;
    }

    .content{
        position: relative;
        transition: top 0.3s;
        top:0%;
    }

    .more-content{
        transition: top 0.35s;
        position: absolute;
        top:0; left:0;
        width:100%;
        height:100%;
        top:100%;
       // background-color: pink;
    }

    &:hover{
        color: rgba($secondary-color, 1) !important;
        .more-content{ top:40%}
        .content{ top:-50%}
        .content.slight-move{ top:-20%}
        .container-overlay{ opacity:0.1}
        .dark-overlay{ background-color:rgba(#fff,0.7); }
        .blend-overlay{ background-color:rgba(#333,1); }
    }
}

.show-more-content-news{
    position: relative;
    overflow: hidden;
    transition:color 0.3s;

    .dark-overlay{
        transition:background 0.4s,opacity 0.4s;
        background-color:rgba(#000,0);
        position: absolute;
        top:0; left:0;
        width:100%;
        height:100%;
    }

    .content{
        position: relative;
        transition: top 0.3s;
        top:0%;
    }

    .more-content{
        transition: top 0.3s,opacity 0.35s;
        position: absolute;
        top:100%;
        margin: auto;
        left: 0;
        margin-top:-1em;
        width:100%;
        background-color: inherit;

        .text{ 
            transition: opacity 0.35s;
            opacity: 0;
        }

    }

    &:hover{
        .more-content .text{ opacity:1}
        .content{ top:-20%}
        .dark-overlay{ background-color:rgba(#000,0.4); }
    }
}

