
/******************  News Slider Demo-1 *******************/

.post-slide {
  overflow: hidden;
  margin-right: 15px;
  background-color: #fff !important;

  .post-img {
    float: left;
    width: 50%;
    position: relative;
    margin-right: 30px;

    img {
      width: 100%;
      height: auto;
    }
  }

  .post-date {
    background: #ec3c6a;
    color: #fff;
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    padding: 2% 3%;
    width: 60px;
    height: 60px;
    text-align: center;
    transition: all 0.50s ease;
  }

  .date {
    display: block;
    font-size: 20px;
    font-weight: 700;
  }

  .month {
    display: block;
    font-size: 11px;
    text-transform: uppercase;
  }

  .post-review {
    padding: 5% 3% 1% 0;
    border-top: 3px solid #38cfd8;
  }

  &:hover .post-review {
    border-top-color: #ec3c6a;
  }

  .post-title {
    margin: 0 0 10px 0;

    a {
      font-size: 14px;
      color: #333;
      text-transform: uppercase;

      &:hover {
        text-decoration: none;
        font-weight: bold;
      }
    }
  }

  .post-bar {
    padding: 0;
    list-style: none;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 20px;

    &:after {
      border: 1px solid #38cfd8;
      bottom: -10px;
      content: "";
      display: block;
      position: absolute;
      right: 36%;
      width: 25px;
    }

    &:before {
      border: 1px solid #38cfd8;
      bottom: -10px;
      content: "";
      display: block;
      position: absolute;
      right: 36%;
      width: 25px;
      border: 1px solid #ec3c6a;
      right: 32%;
    }

    li {
      color: #555;
      font-size: 10px;
      margin-right: 10px;
      display: inline-block;

      a {
        font-size: 13px;
        text-decoration: none;
        text-transform: uppercase;
        color: #ec3c6a;

        &:hover {
          color: #ec3c6a;
        }
      }

      i {
        color: #777;
        margin-right: 5px;
      }
    }
  }

  .post-description {
    font-size: 12px;
    line-height: 21px;
    color: #444454;
  }
}

.owl-theme .owl-controls {
  margin-top: 30px;

  .owl-page {
    span {
      background: #fff;
      border: 2px solid #37a6a4;
    }

    &.active span {
      background: #37a6a4;
    }
  }

  &.clickable .owl-page:hover span {
    background: #37a6a4;
  }
}

@media only screen and (max-width: 990px) {
  .post-slide {
    .post-img {
      width: 100%;
    }

    .post-review {
      width: 100%;
      border-bottom: 4px solid #ec3c6a;
    }

    .post-bar {
      &:before {
        left: 0;
      }

      &:after {
        left: 25px;
      }
    }
  }
}

/******************  News Slider Demo-2 *******************/


.post-slide2 {
  margin: 0 15px;
  box-shadow: 0 1px 2px rgba(43, 59, 93, 0.3);
  margin-bottom: 2em;

  .post-img {
    overflow: hidden;

    img {
      width: 100%;
      height: auto;
      transform: scale(1);
      transition: all 1s ease-in-out 0s;
    }
  }

  &:hover .post-img img {
    transform: scale(1.08);
  }

  .post-content {
    background: #fff;
    padding: 20px;
  }

  .post-title {
    font-size: 17px;
    font-weight: 600;
    margin-top: 0;
    text-transform: capitalize;

    a {
      display: inline-block;
      color: #808080;
      transition: all 0.3s ease 0s;

      &:hover {
        color: #3d3030;
        text-decoration: none;
      }
    }
  }

  .post-description {
    font-size: 15px;
    color: #676767;
    line-height: 24px;
    margin-bottom: 14px;
  }

  .post-bar {
    padding: 0;
    margin-bottom: 15px;
    list-style: none;

    li {
      color: #676767;
      padding: 2px 0;

      i {
        margin-right: 5px;
      }

      a {
        display: inline-block;
        font-size: 12px;
        color: #808080;
        transition: all 0.3s ease 0s;

        &:after {
          content: ",";
        }

        &:last-child:after {
          content: "";
        }

        &:hover {
          color: #3d3030;
          text-decoration: none;
        }
      }
    }
  }

  .read-more {
    display: inline-block;
    padding: 10px 15px;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    background: #e7989a;
    border-bottom-right-radius: 10px;
    text-transform: capitalize;
    transition: all 0.30s linear;

    &:hover {
      background: #333;
      text-decoration: none;
    }
  }
}

/******************  News Slider Demo-3 *******************/

.post-slide3 {
  margin: 0 15px;
  padding: 0 25px 20px 25px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(43, 59, 93, 0.3);
  margin-bottom: 2em;

  .post-img {
    position: relative;
    margin-bottom: 20px;

    img {
      width: 100%;
      height: auto;
    }
  }

  .post-icon {
    width: 60px;
    height: 60px;
    display: block;
    position: absolute;
    bottom: 25px;
    left: 25px;
    text-align: center;
    background: #333;
    opacity: 0;
    border-radius: 3px;
    transition: all 300ms ease-in-out 0s;
  }

  &:hover .post-icon {
    opacity: 1;
  }

  .post-icon i {
    font-size: 20px;
    color: #fff;
    line-height: 60px;
  }

  .post-bar {
    margin: 0;
    padding: 0;
    list-style: none;
    text-transform: uppercase;

    li {
      display: inline-block;
      font-size: 15px;
      color: #676767;
      margin-right: 5px;

      &:after {
        content: "/";
        margin-left: 5px;
      }

      &:last-child:after {
        content: "";
      }

      a {
        color: #8e44ad;
        transition: all 0.3s ease 0s;

        &:hover {
          color: #333;
          text-decoration: none;
        }

        &:after {
          content: ",";
          margin: 0 5px;
        }

        &:last-child:after {
          content: "";
        }
      }
    }
  }

  .post-title {
    margin: 15px 0;
    text-transform: capitalize;

    a {
      font-size: 22px;
      font-weight: 600;
      color: #3c3c3c;
      transition: all 300ms linear 0ms;

      &:hover {
        color: #8e44ad;
        text-decoration: none;
      }
    }
  }

  .post-description {
    font-size: 16px;
    color: #676767;
    line-height: 24px;
    padding-bottom: 15px;
  }

  .read-more {
    display: inline-block;

    &:hover {
      text-decoration: none;
    }

    i {
      font-size: 19px;
      color: #333;
      margin-right: 5px;
      transition: all 0.40s linear 0s;
    }

    span {
      font-size: 16px;
      color: #333;
      opacity: 0;
      text-transform: uppercase;
      transition: all 0.40s linear 0s;
    }

    &:after {
      content: "";
      display: block;
      width: 40%;
      position: relative;
      bottom: -20px;
      border-bottom: 3px solid #333;
      opacity: 0;
      transition: all 0.40s linear 0s;
    }
  }

  &:hover .read-more {
    span, &:after {
      opacity: 1;
    }
  }

  .read-more:hover {
    span, i, &:after {
      color: #8e44ad;
      border-bottom-color: #8e44ad;
    }
  }
}

/******************  News Slider Demo-4 *******************/

.post-slide4 {
  margin: 0 10px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(43, 59, 93, 0.3);
  margin-bottom: 2em;

  .post-info {
    padding: 5px 10px;
    margin: 0;
    list-style: none;

    li {
      display: inline-block;
      margin: 0 5px;

      i {
        margin-right: 8px;
      }

      a {
        font-size: 11px;
        font-weight: bold;
        color: #7e828a;
        text-transform: uppercase;

        &:hover {
          color: #1dcfd1;
          text-decoration: none;
        }
      }
    }
  }

  .post-img {
    position: relative;

    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      background: rgba(0, 0, 0, 0.6);
      transition: opacity 0.40s linear 0s;
    }
  }

  &:hover .post-img:before {
    opacity: 1;
  }

  .post-img img {
    width: 100%;
    height: auto;
  }

  .read {
    position: absolute;
    bottom: 30px;
    left: 50px;
    font-size: 14px;
    color: #fff;
    text-transform: capitalize;
    opacity: 0;
    transition: all 0.40s linear 0s;
  }

  &:hover .read {
    opacity: 1;
  }

  .read:hover {
    text-decoration: none;
    color: #1dcfd1;
  }

  .post-content {
    padding: 40px 15px;
    position: relative;
  }

  .post-author {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    position: absolute;
    top: -45px;
    right: 10px;
    overflow: hidden;
    border: 4px solid #fff;

    img {
      width: 100%;
      height: auto;
    }
  }

  .post-title {
    font-size: 14px;
    font-weight: bold;
    color: #1dcfd1;
    margin: 0 0 10px 0;
    text-transform: uppercase;
    transition: all 0.30s linear 0s;

    &:after {
      content: "";
      width: 25px;
      display: block;
      margin-top: 10px;
      border-bottom: 4px solid #333;
    }
  }

  .post-description {
    font-size: 13px;
    color: #555;
    margin-bottom: 20px;
  }
}

/******************  News Slider Demo-5 *******************/

