/******************  News Slider Demo-7 *******************/

.post-slide7 {
  padding: 0 10px;
  transform: translateY(0);
  transition: all 0.30s ease 0s;

  &:hover {
    transform: translateY(-10px);
  }

  .post-img {
    position: relative;

    img {
      width: 100%;
      height: auto;
    }

    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      background-image: linear-gradient(to left, #c9cd19, #5fc55a, #00b18d, #0096a7, #0e789d);
      // background: linear-gradient(to left, rgba(210, 130, 19, 0.7), rgba(170, 55, 114, 0.7));
      // transform: translateY(-100%);
      transition: all 0.70s ease 0s;
    }
  }

  &:hover .post-img:after {
    transform: translateY(0);
    opacity: 0.3;
  }

  .post-img:before {
    content: "";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    width: 100%;
    height: 100%;
    text-align: center;
    position: absolute;
    top: 20%;
    font-size: 30px;
    color: #fff;
    transition: all 0.50s ease 0s;
    z-index: 1;
  }

  &:hover .post-img:before {
    // content: url('../images/emblem.png');
    top: 20%;
  }

  .icons {
    position: absolute;
    bottom: -16px;
    left: 30px;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    overflow: hidden;
    z-index: 1;

    img {
      width: 100%;
      height: auto;
    }
  }

  .post-review {
    border-bottom: 3px solid $secondary-color;
    // border-left: 1px solid;
    // border-right: 1px solid;
    border-top: none;
    padding: 5px 20px 25px;
    background: #fff;
    position: relative;

    &:after {
      content: "";
      width: 90%;
      height: 10px;
      position: absolute;
      top: 100%;
      left: 5%;
      opacity: 0;
      background: rgba(0, 0, 0, 0) radial-gradient(ellipse at center center, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0) 80%);
      transform: translateY(0);
      transition: all 0.30s ease 0s;
    }
  }

  &:hover .post-review:after {
    opacity: 1;
    transform: translateY(5px);
  }

  .post-bar {
    padding: 0;
    list-style: none;

    li {
      display: inline-block;
      font-size: 16px;
      font-family: serif,Arial;
      color: #555;
      margin-right: 10px;
      text-transform: capitalize;

      i {
        color: $primary-color;
        margin-right: 8px;
      }
    }
  }

  .post-title {
    margin: 0 0 20px 0;
    color: #555;
    font-weight: bold;
    font-size: 18px;
  }

  .post-description {
    font-size: 15px;
    line-height: 21px;
    color: #808080;
  }

  .read {
    text-transform: capitalize;
    font-size: 15px;
    color: #9c4a6c;

    i {
      margin-left: 10px;
    }

    &:hover {
      text-decoration: none;
      color: #333;
    }
  }
}

